.btn {
  display: inline-flex;
  justify-content: center;
  padding: 15px 54px;

  color: $color-default-white;
  font-family: $ff-roboto;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 0.6px;
  text-transform: uppercase;

  background-color: transparent;
  border: 4px solid $color-default-white;
  cursor: pointer;

  transition: opacity $trans-default;

  &:disabled {
    opacity: 0.5;
    pointer-events: none;
  }

  &:hover {
    color: #103aae;
    background-color: $color-default-white;
  }

  &:focus {
    color: #103aae;
    background-color: $color-default-white;
    outline: none;
  }

  &:active {
    color: $color-default-black;
  }

  @media (min-width: $vp-1200) {
    &:hover {
      background-color: $color-default-white;
    }
  }

  @media (min-width: $vp-768) and (max-width: 1199px) {
    padding: 15px 29px 13px 29px;
  }

  &--inverted {
    color: $color-default-black;
    border: 2px solid $color-default-black;
    padding: 19px 36px;

    &:hover {
      color: $color-default-white;
      border: none;
      padding-top: 20px;
      background: linear-gradient(151deg, #ed0233 0%, #003eb7 100%);
    }

    &:focus {
      color: $color-default-white;
      border: none;
      padding-top: 20px;
      background: linear-gradient(151deg, #ed0233 0%, #003eb7 100%);
    }

    &:active {
      color: rgba(255, 255, 255, 0.7);
    }
  }

  &--red {
    background-color: $color-torch-red;
    border: none;
  }

  &--tabs {
    font-size: 20px;
    font-weight: 400;
    line-height: 150%;
    letter-spacing: 0.2px;
    text-transform: none;
    color: $color-default-black;
    border: none;
    padding: 0;

    @media screen and (max-width: 1199px) {
      font-size: 18px;
      padding: 0;
    }

    @media screen and (max-width: 767px) {
      font-size: 16px;
      letter-spacing: 0;
    }
  }

  &--questions {
    width: 260px;
    font-size: 18px;
    font-weight: 500;
    line-height: 1;
    letter-spacing: 0.2px;
    text-transform: none;
    color: $color-default-black;
    border: 1px solid rgba(16, 38, 84, 0.2);
    padding: 17px;

    &:active {
      color: rgba(255, 255, 255, 0.7);
    }

    @media screen and (max-width: 1199px) {
      width: 162px;
    }

    @media screen and (max-width: 767px) {
      font-size: 16px;
      padding: 13px 14px;
      width: auto;
      min-width: 141px;
    }
  }
}
